:root {
  --black: #150605;
  --red: #EA453C;
  --pink: #F47B6E;
  --light-blue: #EFFBFE;
  --blue: #ACE6EC;
}

* {
  box-sizing: border-box;
}

html, body {
  font-family: 'Oswald', sans-serif;
  margin: 0;
  padding: 0;
}

.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: whitesmoke;
}

main.photos {
  padding: 5px 10px;
  display: grid;
  grid-gap: 2px;
  grid-auto-rows: 160px;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  grid-auto-flow: dense;
}

main.cart-page {
  padding: 10px 30px;
}

.cart-page > h1 {
  text-align: center;
}

.cart-page .total-cost {
  text-align: right;
  font-size: 1.3em;
}

.cart-page div.order-button {
  display: flex;
  justify-content: center;
}

div.order-button > button {
  border-color: var(--black);
  cursor: pointer;
  padding: 15px;
  transition: all .2 ease-in-out;
}

div.order-button > button:disabled {
  border-color: lightgray;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

.cart-item img {
  margin-right: auto;
  margin-left: 10px;
}

img.image-grid {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-container {
  position: relative;
}

.favorite {
  color: var(--red);
  /*font-size: 1.2rem;*/
  font-size: 1.75rem;
  position: absolute;
  top: 5px;
  left: 5px;
  cursor: pointer;
}

.price {
  font-size: 1.75rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ACE6EC;
  /*opacity: 0.9;*/
}

.ri-heart-fill {
  opacity: .7;
}

.ri-delete-bin-line, .ri-delete-bin-fill {
  font-size: 1.3em;
  cursor: pointer;
}

.cart {
  color: var(--blue);
  /*font-size: 1.2rem;*/
  font-size: 1.75rem;
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

.wide {
  grid-column: span 2;
}

.tall {
  grid-row: span 2;
}

.big {
  grid-column: span 2;
  grid-row: span 2;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--pink);
  color: var(--light-blue);
  padding: 0 20px;
  box-shadow: 0px 2px 10px -5px rgba(21,6,5,1);
}

a {
  text-decoration: none;
}

footer {
  background-color: var(--pink);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: inherit;

  bottom: 0;
  width: 100%;
  margin-top: auto;
  gap: 7px;
  padding: 10px;

  box-shadow: 0px -2px 10px -5px rgba(21,6,5,1);
}

footer p {
  font-size: 18px;
  padding: 0px;
  margin: 0px;
}

.icon {
  width: 25px;
  height: 25px;
}


.footer-link {
  display: flex;
  align-items: center;
  gap: 3px;
}

.link {
  color: var(--light-blue);
}

.link:hover {
  color: #ACE6EC;
}



